import { Block } from '@cetus/design'
import { Box, VStack } from '@chakra-ui/react'
import { useState } from 'react'
import ProfileMenus from '../ProfileMenus'
import Dca from './dca'
import Limit from './limit'

function ProfileOrders() {
  const tabList = [
    {
      label: 'Limit Orders',
      value: 'limitOrders'
    },
    {
      label: 'DCA',
      value: 'dca'
    }
  ]
  const [currentTab, setCurrentTab] = useState('limitOrders')
  return (
    <Block p="0 16px 16px" bg="block_color_opacity.50" backdropFilter="blur(20px)">
      <VStack align="flex-start" gap="20px">
        <Box w="100%" borderBottom="1px solid" borderColor="border">
          <ProfileMenus
            type="tab"
            currentTab={currentTab}
            tabs={tabList}
            onTabChange={tab => setCurrentTab(tab.value)}
            textStyle={{
              fontSize: '16px'
            }}
            wrapStyle={{
              bg: 'none'
            }}
          />
        </Box>
        {currentTab === 'limitOrders' && <Limit />}
        {currentTab === 'dca' && <Dca />}
      </VStack>
    </Block>
  )
}

export default ProfileOrders
