import { Block } from '@cetus/design'
import useWindowWidth from '@cetus/hooks/src/useWindowWidth'
import { HStack, Switch, Text } from '@chakra-ui/react'
import SearchAssetsInput from './SearchAssetsInput'
type FilterAssetsProps = {
  inputValue: string
  changeInputValue: (value: string) => void
  isShowUnknownCoin: boolean
  handleIsShowUnknownCoin: (event: React.ChangeEvent<HTMLInputElement>) => void
  isHideLowAsset: boolean
  handleIsHideLowAsset: (event: React.ChangeEvent<HTMLInputElement>) => void
  unknownCoinCount: number
}
function FilterAssets({
  inputValue,
  changeInputValue,
  isShowUnknownCoin,
  handleIsShowUnknownCoin,
  isHideLowAsset,
  handleIsHideLowAsset,
  unknownCoinCount
}: FilterAssetsProps) {
  const { isApp, windowWidth } = useWindowWidth()
  return (
    <HStack w="100%" justify="space-between">
      <Text fontSize="16px" color="text_caption">
        Wallet Holdings
      </Text>
      <HStack>
        <SearchAssetsInput inputValue={inputValue} changeInputValue={changeInputValue} />
        <Block w="unset" borderRadius="8px" p="0px 8px" h="32px" bg="background">
          <HStack h="100%" justify="space-between">
            <Text whiteSpace="nowrap" fontSize="12px">
              Show Unknown Coin ({unknownCoinCount})
            </Text>
            <Switch isChecked={isShowUnknownCoin} onChange={handleIsShowUnknownCoin} />
          </HStack>
        </Block>
        <Block w="unset" borderRadius="8px" p="0px 8px" h="32px" bg="background">
          <HStack h="100%" justify="space-between">
            <Text whiteSpace="nowrap" fontSize="12px">
              Hide Low Asset
            </Text>
            <Switch isChecked={isHideLowAsset} onChange={handleIsHideLowAsset} />
          </HStack>
        </Block>
      </HStack>
    </HStack>
  )
}

export default FilterAssets
