import { IconText } from '@/components/common/IconText'
import DcaModal from '@/components/profile/Orders/DcaModal'
import useDcaItemActions from '@/hooks/dca/useDcaItemActions'
import { CetusTooltip } from '@cetus/design'
import useExplorer from '@cetus/hooks/src/useExplorer'
import useWindowWidth from '@cetus/hooks/src/useWindowWidth'
import { Icon } from '@cetus/ui-kit'
import { cancelBubble } from '@cetus/utils'
import { Button, Center, HStack, VStack } from '@chakra-ui/react'
import { useState } from 'react'

export default function DcaTableAction({ orderInfo, isActiveOrder = true }: { orderInfo: any; isActiveOrder?: boolean }) {
  const { isApp } = useWindowWidth()
  const [isOpenDetails, setIsOpenDetails] = useState(false)
  const { getExplorerUrl } = useExplorer()
  const { isClaimLoading, toClaim, closeOrderAction, isCloseLoading } = useDcaItemActions()
  return (
    <>
      {isActiveOrder && (
        <HStack justify="flex-end">
          <Button
            isLoading={isClaimLoading}
            onClick={() => toClaim(orderInfo)}
            disabled={orderInfo?.outBalance <= 0 || isClaimLoading}
            h="32px"
            fontSize="12px"
            borderRadius="8px"
            fontWeight="500"
            p="0 8px"
          >
            Claim
          </Button>
          <CetusTooltip
            placement="bottom-end"
            tooltip={
              <VStack align="flex-start">
                <IconText
                  text="View Details"
                  onClick={() => {
                    setIsOpenDetails(true)
                  }}
                />
                <IconText
                  text="View on Explorer"
                  onClick={() => {
                    window.open(getExplorerUrl(orderInfo.orderID, 'poolAddress'))
                  }}
                />
                <IconText
                  text={orderInfo?.outBalance <= 0 ? 'Close' : 'Close and Withdraw'}
                  onClick={
                    isCloseLoading
                      ? () => {}
                      : () => {
                          closeOrderAction(orderInfo)
                        }
                  }
                />
              </VStack>
            }
          >
            <Center>
              <Button
                variant="outline"
                w="32px"
                h="32px"
                p="0"
                borderRadius="8px"
                sx={{
                  _hover: {
                    svg: {
                      fill: 'primary'
                    }
                  }
                }}
              >
                <Icon svgFill={isApp ? 'primary' : 'text_paragraph'} xlinkHref="#icon-icon_more" svgW="16px" svgH="16px" />
              </Button>
            </Center>
          </CetusTooltip>
        </HStack>
      )}
      {!isActiveOrder && (
        <HStack justify="flex-end">
          <Button
            onClick={() => {
              setIsOpenDetails(true)
            }}
            h="32px"
            fontSize="12px"
            variant="outline"
            borderRadius="8px"
            fontWeight="500"
            p="0 8px"
          >
            Details
          </Button>
          <Button
            onClick={e => {
              cancelBubble(e)
              window.open(getExplorerUrl(orderInfo.orderID, 'poolAddress'))
            }}
            w="32px"
            p="0"
            h="32px"
            fontSize="12px"
            borderRadius="8px"
            variant="outline"
            _hover={{
              svg: {
                fill: 'primary'
              }
            }}
          >
            <Icon svgW="20px" xlinkHref="#icon-icon_link1" />
          </Button>
        </HStack>
      )}
      {isOpenDetails && (
        <DcaModal
          isOpen={isOpenDetails}
          onClose={() => setIsOpenDetails(false)}
          orderInfo={orderInfo}
          isActiveOrder={isActiveOrder}
          isClaimLoading={isClaimLoading}
          toClaim={toClaim}
        />
      )}
    </>
  )
}
