import useVaultsActionStore from '@/store/vaults-v2/useVaultsAction'
import { Button, HStack } from '@chakra-ui/react'
import { useEffect, useState } from 'react'
import VaultModal from '../modal'

export default function VaultsTableAction({ poolInfo }: { poolInfo: any }) {
  console.log('🚀 ~ VaultsTableAction ~ poolInfo:', poolInfo)
  const [currentTab, setCurrentTab] = useState<'Deposit' | 'Withdraw'>('Deposit')
  const [isOpenVaultModal, setIsOpenVaultModal] = useState(false)
  const { clearVaultsActionData } = useVaultsActionStore()
  useEffect(() => {
    console.log('🚀🚀🚀 ~ ActionsBlock.tsx:22 ~ useEffect ~ clearVaultsActionData:')
    clearVaultsActionData()
  }, [isOpenVaultModal])
  return (
    <>
      <HStack justify="flex-end">
        <Button
          onClick={() => {
            setCurrentTab('Deposit')
            setIsOpenVaultModal(true)
          }}
          h="32px"
          fontSize="12px"
          borderRadius="8px"
          fontWeight="500"
          p="0 8px"
        >
          Deposit
        </Button>
        <Button
          onClick={() => {
            setCurrentTab('Withdraw')
            setIsOpenVaultModal(true)
          }}
          variant="outline"
          h="32px"
          fontSize="12px"
          borderRadius="8px"
          fontWeight="500"
          p="0 8px"
        >
          Withdraw
        </Button>
      </HStack>
      {isOpenVaultModal && (
        <VaultModal
          key={poolInfo.vaultId}
          isOpen={isOpenVaultModal}
          setIsOpen={setIsOpenVaultModal}
          displayTokenA={poolInfo.displayTokenA}
          displayTokenB={poolInfo.displayTokenB}
          feeDisplay={poolInfo.feeDisplay}
          clmmPool={poolInfo.clmmPoolAddress}
          category={poolInfo.category}
          vaultId={poolInfo.vaultId}
          isReverse={poolInfo.isReverse}
          currentTab={currentTab}
        />
      )}
    </>
  )
}
